
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    background-color: rgb(207, 206, 206);
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color:black
}

h2{
    color: rgb(122, 2, 2);
}

.booklist{
    width: 90vw;
    max-width: 1170px;
    margin: 5rem auto;
    display: grid;
    gap: 2rem;
}
.book{
    
    height: 40vw;
    width: 70vw;
    background:#ffff;
    border-radius: 1rem;
    padding:6rem;
    border-radius: 5%;

}
.book img{
    width: 12vw;
    height: 18vw;
}
.book h2{
    
    margin: .5rem;
}
.book h4{
    padding: .2rem .5rem;
}
button{
    margin: 15px; 
    padding: .2rem;
    height: 30px;
    width: 120px;
    background-color: antiquewhite;
    text-decoration: double;
    text-transform: capitalize;
    text-align: center;
}